/* Animations Below */
.WeatherComponent__FlipWeatherCard__3_lVw {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    -webkit-transition: -webkit-transform 0.9s;
    transition: -webkit-transform 0.9s;
    transition: transform 0.9s;
    transition: transform 0.9s, -webkit-transform 0.9s;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.WeatherComponent__StaticWeatherCard__2zwLQ {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
}
.WeatherComponent__WeatherCard__22IWH:hover .WeatherComponent__FlipWeatherCard__3_lVw {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.WeatherComponent__WeatherCardFront__OWRgk, .WeatherComponent__WeatherCardBack__USFBG {
