.WeatherComponent__drop__2jkyq {
    position: relative;
    bottom: 0%;
    top: -15px;
    width: 1px;
    float: right;
    border-radius: 10px;
    height: 30px;
    -webkit-animation: WeatherComponent__drop__2jkyq 0.3s linear infinite;
            animation: WeatherComponent__drop__2jkyq 0.3s linear infinite;
    background-color: rgba(161, 161, 161, 0.438);
  }

  @-webkit-keyframes WeatherComponent__drop__2jkyq {
    0% {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
    100% {
      -webkit-transform: translateY(199px);
              transform: translateY(199px);
    }
  }

  @keyframes WeatherComponent__drop__2jkyq {
    0% {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
    100% {
      -webkit-transform: translateY(199px);
              transform: translateY(199px);
    }
  }

